$color-black: #000;
$color-white: #fff;
$color-orangered: #ff4500;
$color-firebrick: firebrick;
$color-orange: #ffa500;
$color-green: #008000;
$color-darkgreen: #004200;
$bg-position: top center;
$bg-repeat: no-repeat;
$bg-size: cover;

$font-Vietnam: 'Be Vietnam', sans-serif;
$font-Playfair: 'Playfair Display', serif;
$font-weight4: 400;
$font-weight7: 700;
$font-style-normal: normal;
$font-style-italic: italic;
$font-style-oblique: oblique;

$text-uppercase: uppercase;

// Global Settings
html,
body {
    height: 100%;
    background-color: $color-white;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $font-Playfair;
}

p {
    font-family: $font-Vietnam;
}

.btn {
    text-transform: $text-uppercase;
    font-weight: $font-weight7;
    letter-spacing: .5rem;
    line-height: 1;
    padding: .6rem .75rem;
    font-size: .6875rem;
    line-height: 1.5;
    border-radius: 0;
}

// Landing Page CSS 

.navbar {
    z-index: 1000;
}

.navbar {
    transition: background .3s ease-out;
}

.bg-hover-white:hover,
.navbar.fixed-top.bg-fixed-white {
 background:#fff!important
}


.navbar-hover-light:hover .navbar-nav .nav-link {
    color:rgba(0,0,0,.65)
}


.navbar-hover-light:hover .navbar-toggler {
    color:rgba(0,0,0,.65);
    border-color:transparent
}

.navbar-hover-light:hover .navbar-text {
    color:rgba(0,0,0,.65) 
}

.navbar-light .navbar-nav .nav-link {
    font-family: $font-Playfair;
    font-weight: $font-weight7;
    color: $color-white;
    text-transform: $text-uppercase;
    letter-spacing: .22rem;
    font-size: .9rem;
}

.navbar-nav a:hover {
    color: $color-firebrick;
}


#carousel {
    background-color: $color-darkgreen;
}

#carousel h1 {
    text-shadow: .125rem .125rem .5rem $color-black;
    letter-spacing: .2rem;
}

#carousel p {
    text-shadow: .125rem .125rem .5rem $color-black;
}

.carousel-indicators li {
    border-radius: 50%;
    width: 0.75rem;
    height: 0.75rem;
    background-color: $color-white;
}

#twoBoxes .card,
#threeBoxes .card {
    border: none !important;
}

#twoBoxes h1,
#threeBoxes h1 {
    text-shadow: .125rem .125rem .5rem $color-black;
}

.leftBox,
.rightBox {
    padding-left: .5rem;
    padding-right: .5rem;
}

#social-media img {
    max-width: 250px;
    max-height: 125px;
}

#sm-icon {
    border-width: 3px;
    border-style: solid;
    border-color: $color-orange;
    border-image: initial;
    border-radius: 50px 50px;
    padding: 20px 5px !important;
}

#footer {
    background-color: $color-black;
    color: $color-white;
}

.drk:after {
    content:"";
    display:block;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:$color-black;
    opacity:0.25;
}

@media screen and (max-width: 47.998rem) {
    #twoThree,
    #threeThree {
        padding-top: 1rem;
    }

    #sm-icon {
        margin-top: .5rem !important;
        margin-bottom: .5rem !important;
    }

}


@media screen and (min-width: 62rem) {
    .carousel-control-next-icon,
    .carousel-control-prev-icon {
        width: 2.5rem;
        height: 2.5rem;
    }
}

@media (min-width: 48rem) and (max-width: 80rem) { 
    h1 {
        font-size: 2rem !important;
    }
 }